<template>
  <div id="feature">
    <div class="buttonZone">
      <p @click="showAddOneDialog" class="addCategoryBtn">添加特色</p>
      <p class="simulatedLine"></p>
      <p class="submitBtn submit-button" @click="showEditReasonDialog">
        提交审核
      </p>
      <p class="simulatedLine"></p>
      <p class="giveUpModificationBtn" @click="resetListAndOrder">清除修改</p>
    </div>
    <Scroll
      class="scroll-content"
      ref="scroll"
      @scroll="printPosition"
      :probe-type="3"
    >
      <content-list
        :list="liveList"
        @deleteCategoryClick="deleteCategoryItem"
        @newImageUpload="newImageUpload"
        @emptyNewTitle="emptyNewTitleToast"
        @findSameTitle="findSameTitleToast"
        @updateTitle="updateTitle"
        @titleNoChange="titleNoChange"
        @showEditDialog="showEditDialog"
      ></content-list>
    </Scroll>
    <transition name="super">
      <div class="right-content-cover-absolute" v-if="isShowAddOneDialog">
        <add-one
          :list="list"
          @ensureClick="addOneFeatureObj"
          @cancelClick="cancelClickOfAddOne"
          @emptyCategoryName="emptyCategoryNameToast"
          @hadSameTitleFromList="hadSameTitleFromListToast"
          @imageUploadSuccess="imageUploadSuccessToast"
          ref="addone"
        ></add-one>
      </div>
    </transition>
    <transition name="super">
      <div class="right-content-cover-absolute" v-if="isShowEditOneDialog">
        <edit-one
          :list="liveList"
          :editIndex="editIndex"
          :editContent="editContent"
          @cancelClick="cancelClickofEditOne"
          @ensureClick="ensureClickofEditOne"
        ></edit-one>
      </div>
    </transition>
    <transition name="super">
      <div class="right-content-cover-absolute" v-show="isShowEditReasonDialog">
        <edit-reason-dialog
          @ensureClick="deployOrder"
          @cancelClick="cancelClickOfEditReasonDialog"
          ref="editReasonDialog"
        ></edit-reason-dialog>
      </div>
    </transition>

    <div class="absoluteFullLayout" v-show="isShowCoverPage"></div>

    <back-top
      @click="scrollToTop"
      v-show="ifShowBackTop"
      title="返回顶部"
    ></back-top>

    <loading :isLoading="isLoading"></loading>
  </div>
</template>

<script>
import Scroll from "../../../components/common/scroll/Scroll.vue";
import ContentList from "./childComponents/contentList/ContentList.vue";
import { debounce } from "../../../common/utils";
import AddOne from "./childComponents/addOne/AddOne.vue";
import EditOne from "./childComponents/editOne/editOne.vue";
import { $deployOrder } from "../../../network/adminTools/category";
import { $uploadStreamMediaFile } from "../../../network/common/common";
import EditReasonDialog from "../../../components/content/adminTools/editReasonDialog/EditReasonDialog.vue";
import BackTop from "../../../components/common/backTop/BackTop.vue";
import Loading from "../../../components/common/loading/Loading.vue";
import { $getFeatures } from "../../../network/adminTools/feature";
import { arrayDeepEqualDetect } from "../../../common/utils";
export default {
  name: "e-Feature",
  beforeRouteEnter(to, from, next) {
    if (window.localStorage.getItem("isStaff") === "true") {
      window.localStorage.setItem("currentIndex010001", 1);
      next();
    } else {
      next({
        path: "/tools/login",
      });
      window.localStorage.removeItem("list");
      window.localStorage.removeItem("order");
      window.localStorage.removeItem("currentIndex010001");
      window.localStorage.removeItem("userID");
      window.localStorage.removeItem("username");
      window.localStorage.removeItem("authToken");
      window.localStorage.removeItem("isStaff");
      window.localStorage.removeItem("isSuperUser");
      window.localStorage.removeItem("isProvider");
    }
  },
  async activated() {
    const response = await $getFeatures();
    if (response.Code === 200 && response.msg === "success") {
      // this.$toast.success('列表获取成功', 240, 170, 500);
      const flag = arrayDeepEqualDetect(
        response.details,
        window.JSON.parse(window.JSON.stringify(this.originalList))
      );
      console.log(flag);
      if (flag) return;
      else {
        this.originalList = response.details;
        this.liveList = window.JSON.parse(
          window.JSON.stringify(this.originalList)
        );
      }
    } else {
      this.$toast.error("获取列表失败", 240);
    }

    window.addEventListener("beforeunload", this.beforeUnloadHandler, false);
  },
  mounted() {
    if (this.$refs.scroll) {
      this.debounceRefresh = debounce(this.$refs.scroll.refresh, 500);
    }
    this.$bus.$on("itemImageLoad", () => {
      this.debounceRefresh();
    });
  },
  deactivated() {
    this.$bus.$off("itemImageLoad");
  },
  beforeRouteLeave(to, from, next) {
    if (this.editMark) {
      if (confirm("您有修改未提交，确认离开？")) {
        this.liveList = window.JSON.parse(
          window.JSON.stringify(this.originalList)
        );
        if (this.orderList.add.length !== 0) {
          this.orderList.add.splice(0, this.orderList.add.length);
        }
        if (this.orderList.update.length !== 0) {
          this.orderList.add.splice(0, this.orderList.update.length);
        }
        if (this.orderList.delete.length !== 0) {
          this.orderList.add.splice(0, this.orderList.delete.length);
        }
        next();
      } else {
        window.localStorage.setItem("currentIndex010001", 1);
        this.$bus.$emit("setCurrentIndex010001To1");
        next(false);
      }
    } else {
      this.liveList = window.JSON.parse(
        window.JSON.stringify(this.originalList)
      );
      if (this.orderList.add.length !== 0) {
        this.orderList.add.splice(0, this.orderList.add.length);
      }
      if (this.orderList.update.length !== 0) {
        this.orderList.add.splice(0, this.orderList.update.length);
      }
      if (this.orderList.delete.length !== 0) {
        this.orderList.add.splice(0, this.orderList.delete.length);
      }
      next();
    }
  },
  data() {
    return {
      isLoading: false,
      debounceRefresh: null,
      isShowAddOneDialog: false,
      isShowEditOneDialog: false,
      isShowCoverPage: false,
      activeEditID: -1,
      isShowEditReasonDialog: false,
      ifShowBackTop: false,

      list: [],
      originalList: [],
      liveList: [],
      orderList: {
        add: [],
        update: [],
        delete: [],
      },
      editMark: false,

      editIndex: 0,
      editContent: [],
    };
  },
  methods: {
    // 删除一个类别数据
    deleteCategoryItem(index) {
      const temp = window.JSON.parse(
        window.JSON.stringify(this.liveList[index])
      ); // 浅拷贝了一份将要被删除的类别数据
      this.liveList.splice(index, 1);

      // 去看原始的list中是否存在该id对应的类别对象
      const findInOriginalList = this.originalList.find(
        (item) => item.id === temp.id
      );
      //const tempOrder = window.JSON.parse(window.JSON.stringify(this.order));
      if (findInOriginalList) {
        this.orderList.delete.push(findInOriginalList); // 保存最原始的name, thumbnail_url
        const findInUpdate = this.orderList.update.find(
          (item) => item.id === temp.id
        );
        if (findInUpdate) {
          const index = this.orderList.add.indexOf(findInUpdate);
          this.orderList.update.splice(index, 1);
        }
      } else {
        const findInAdd = this.orderList.add.find(
          (item) => item.id === temp.id
        );
        if (findInAdd) {
          const index = this.orderList.add.indexOf(findInAdd);
          this.orderList.add.splice(index, 1);
        }
      }
    },
    showAddOneDialog() {
      this.isShowCoverPage = true;
      this.isShowAddOneDialog = true;
    },
    showEditDialog(param) {
      this.isShowCoverPage = true;
      this.isShowEditOneDialog = true;
      // console.log(param);
      this.editIndex = param;
      this.editContent = this.liveList[param];
    },
    // 增加一个类别对象
    addOneFeatureObj(param) {
      this.isShowAddOneDialog = false;
      this.isShowCoverPage = false;
      const { name } = param;
      const findInList = this.liveList.find((item) => item.name === name);
      if (findInList) {
        this.$toast.warn("该类别已存在", 240);
        return;
      }
      /* const tempList = window.JSON.parse(window.JSON.stringify(this.list));
      tempList.push(param);
      this.list = tempList; */
      this.liveList.push(param);
      this.$refs.scroll.refresh();
      setTimeout(() => {
        this.$refs.scroll.scrollTo(0, this.$refs.scroll.scroll.maxScrollY, 500);
      }, 1000); // 渲染需要一定时间

      this.orderList.add.push(param);
      this.isShowEditReasonDialog = true;
      this.isShowCoverPage = true;
    },
    ensureClickofEditOne(param) {
      this.isShowEditOneDialog = false;
      this.isShowCoverPage = false;
      this.orderList.update.push(param);
      this.isShowEditReasonDialog = true;
      this.isShowCoverPage = true;
    },
    cancelClickOfAddOne() {
      this.isShowAddOneDialog = false;
      this.isShowCoverPage = false;
    },
    emptyCategoryNameToast() {
      this.$toast.error("请先填写类别名", 240);
    },
    hadSameTitleFromListToast() {
      this.$toast.error("该类别名已存在", 240);
    },
    imageUploadSuccessToast() {
      this.$toast.success("上传成功", 240);
    },
    async newImageUpload(param) {
      // console.log(param);
      const { index, formData } = param;
      const response = await $uploadStreamMediaFile(formData);
      if (response.Code === 200 && response.msg === "success") {
        this.$toast.success("上传成功", 240);
        const { details } = response;
        const { image_url } = details;

        this.$set(this.liveList[index], "thumbnail", image_url);
        this.activeEditID = this.liveList[index].id;

        const findInAdd = this.orderList.add.find(
          (item) => item.id === this.activeEditID
        );
        if (findInAdd) {
          findInAdd.thumbnail = image_url;
        } else {
          const findInUpdate = this.orderList.update.find(
            (item) => item.id === this.activeEditID
          );
          if (findInUpdate) {
            findInUpdate.thumbnail = image_url;
          } else {
            const obj = {
              id: this.activeEditID,
              thumbnail: image_url,
            };
            this.orderList.update.push(obj);
          }
        }

        this.activeEditID = -1;
      } else {
        alert("更新失败");
      }
    },
    emptyNewTitleToast() {
      this.$toast.warn("类别名不能为空", 240);
    },
    findSameTitleToast() {
      this.$toast.warn("该类别名已存在", 240);
    },
    updateTitle(param) {
      const { index, newText } = param;
      this.activeEditID = this.liveList[index].id;
      this.$set(this.liveList[index], "name", newText); // 好像可以直接进行修改，而删除、添加和查找元素是不能直接进行的

      // 处理工单数据
      // const tempOrder = window.JSON.parse(window.JSON.stringify(this.order));
      const findInAdd = this.orderList.add.find(
        (item) => item.id === this.activeEditID
      );
      if (findInAdd) {
        this.$toast.success("修改成功", 240);
        findInAdd.name = newText;
      } else {
        const findInUpdate = this.orderList.update.find(
          (item) => item.id === this.activeEditID
        );
        if (findInUpdate) {
          if (findInUpdate.name) {
            const original22 = this.originalList.find(
              (item) => item.id === this.activeEditID
            );
            if (original22.name === newText) {
              this.$toast.warn("本次修改后与原类别名称一致", 240, 280);
              if (Object.keys(findInUpdate).length === 2) {
                const index33 = this.orderList.update.indexOf(findInUpdate);
                this.orderList.update.splice(index33, 1);
              } else {
                delete findInUpdate.name;
              }
            } else {
              this.$toast.success("修改成功", 240);
              findInUpdate.name = newText;
            }
          } else {
            this.$toast.success("修改成功", 240);
            findInUpdate.name = newText;
          }
        } else {
          this.$toast.success("修改成功", 240);
          const obj = {
            id: this.activeEditID,
            name: newText,
          };
          this.orderList.update.push(obj);
        }
      }
      this.activeEditID = -1;
    },
    titleNoChange() {
      this.$toast.warn("未检查到更改", 240);
    },
    showEditReasonDialog() {
      this.isShowEditReasonDialog = true;
      this.isShowCoverPage = true;
    },
    async deployOrder() {
      console.log(this.orderList.update.length, "order");
      // console.log(this.order.add.length);
      const { message } = this.$refs.editReasonDialog;
      if (!message) {
        this.$toast.warn("请填写编辑理由", 240);
        return;
      }
      //const orderObj = window.JSON.parse(window.JSON.stringify(this.order));
      if (
        this.orderList.add.length === 0 &&
        this.orderList.update.length === 0 &&
        this.orderList.delete.length === 0
      ) {
        this.$toast.warn("未有有效编辑", 240);
        return;
      }
      if (this.orderList.update.length != 0) {
        var requestPayload = {
          user: {
            // id: this.cookieOperations.getCookie('userId'),
            id: Number(window.localStorage.getItem("userID")),
          },
          version: "1.0.0",
          create_reason: message,
          work_line_id: 1300,
          work_operation: 4300,
          // content: window.JSON.parse(
          //   window.JSON.stringify(this.orderList.update[0])
          // ),
          content: {
            id: this.orderList.update[0].id,
            name: this.orderList.update[0].name,
            thumbnail: this.orderList.update[0].thumbnail,
          },
        };
      }
      if (this.orderList.add.length != 0) {
        var requestPayload = {
          user: {
            // id: this.cookieOperations.getCookie('userId'),
            id: Number(window.localStorage.getItem("userID")),
          },
          version: "1.0.0",
          create_reason: message,
          work_line_id: 1300,
          work_operation: 4200,
          content: window.JSON.parse(window.JSON.stringify(this.orderList.add)),
        };
      }
      if (this.orderList.delete.length != 0) {
        var requestPayload = {
          user: {
            // id: this.cookieOperations.getCookie('userId'),
            id: Number(window.localStorage.getItem("userID")),
          },
          version: "1.0.0",
          create_reason: message,
          work_line_id: 1300,
          work_operation: 4900,
          content: window.JSON.parse(
            window.JSON.stringify(this.orderList.delete[0])
          ),
        };
      }
      console.log(requestPayload, "ro");
      const response = await $deployOrder(requestPayload);
      this.isShowEditReasonDialog = false;
      if (response) {
        const { Code, msg } = response;
        if (Code === 200 && msg === "success") {
          this.$toast.success("提交成功，请等待审核", 240, 200, 1000);
          // this.isShowEditReasonDialog = false;
          this.isShowCoverPage = false;
          this.isShowEditReasonDialog = false;
          this.orderList = {
            add: [],
            update: [],
            delete: [],
          };
          window.localStorage.setItem(
            "order",
            window.JSON.stringify(this.order)
          );

          this.list = window.JSON.parse(
            window.JSON.stringify(this.originalList)
          );
          window.localStorage.setItem("list", window.JSON.stringify(this.list));
          setTimeout(() => {
            this.$toast.success("页面回退到修改前", 240);
          }, 1000);
          this.$refs.scroll.scrollTo(0, 0, 300);
        } else {
          this.$toast.error("提交出错，请稍后再试", 240);
        }
      } else {
        alert("提交失败");
      }
    },
    cancelClickOfEditReasonDialog() {
      this.isShowEditReasonDialog = false;
      this.isShowCoverPage = false;
    },
    cancelClickofEditOne() {
      this.isShowEditOneDialog = false;
      this.isShowCoverPage = false;
    },
    resetListAndOrder() {
      if (
        this.orderList.add.length === 0 &&
        this.orderList.update.length === 0 &&
        this.orderList.delete.length === 0
      ) {
        this.$toast.warn("当前没有修改", 240);
        return;
      }
      this.orderList.add.splice(0, this.orderList.add.length);
      this.orderList.update.splice(0, this.orderList.update.length);
      this.orderList.delete.splice(0, this.orderList.delete.length);

      this.liveList.splice(0, this.liveList.length);
      /* for(const item of this.originalList) {
        const stringifyItem = window.JSON.parse(window.JSON.stringify(item));
        this.liveList.push(stringifyItem);
        // console.log(this.liveList);
      } */
      this.liveList = window.JSON.parse(
        window.JSON.stringify(this.originalList)
      );

      setTimeout(() => {
        this.$toast.success("页面回退到修改前", 240);
      }, 100);
    },
    scrollToTop() {
      this.$refs.scroll.scrollTo(0, 0, 500);
    },
    printPosition(position) {
      this.ifShowBackTop = -position.y > 400;
    },
    beforeUnloadHandler(e) {
      if (this.editMark) {
        e.returnValue = "离开此页面？";
      } else {
        return;
      }
    },
  },
  computed: {
    getAccount() {
      return window.localStorage.getItem("name");
    },
  },
  watch: {
    orderList: {
      handler(newVal) {
        if (
          newVal.add.length !== 0 ||
          newVal.update.length !== 0 ||
          newVal.delete.length !== 0
        ) {
          this.editMark = true;
        } else {
          this.editMark = false;
        }
      },
      deep: true,
    },
  },
  components: {
    Scroll,
    ContentList,
    AddOne,
    EditReasonDialog,
    BackTop,
    Loading,
    EditOne,
  },
};
</script>

